import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    orders: [],
    // orders: [{
    //   id: 'gid://shopify/Order/2106688077903',
    //   name: '#1030',
    //   created_at: 'Mon Mar 16 2020',
    //   customer: { first_name: 'No', last_name: 'Customer' },
    //   total_price: '2978.5',
    //   fulfilled_by: null,
    //   fulfilled_by_id: null,
    //   order_status: 'completed',
    //   order_status_id: 'gid://shopify/Metafield/11874851782735',
    //   fulfillment_status: 'UNFULFILLED',
    //   payment_status: 'PAID',
    //   fulfillment_order_id: null
    // },
    // {
    //   id: 'gid://shopify/Order/2106723205199',
    //   name: '#1031',
    //   created_at: 'Mon Mar 16 2020',
    //   customer: { first_name: 'Mahathir', last_name: 'Jeshan' },
    //   total_price: '57.49',
    //   fulfilled_by: 'fulfilled',
    //   fulfilled_by_id: 'gid://shopify/Metafield/11874871967823',
    //   order_status: null,
    //   order_status_id: null,
    //   fulfillment_status: 'UNFULFILLED',
    //   payment_status: 'PAID',
    //   fulfillment_order_id: null
    // }],
    fulfillmentsOption: [
      {label: 'Unfulfilled', value: 'Unfulfilled'},
      {label: 'Ecourier Inside Dhaka', value: 'Ecourier Inside Dhaka'},
      {label: 'Ecourier Outside Dhaka', value: 'Ecourier Outside Dhaka'},
      {label: 'Pathao Inside Dhaka', value: 'Pathao Inside Dhaka'},
      {label: 'Pathao Outside Dhaka', value: 'Pathao Outside Dhaka'},
      {label: 'Sundarban Inside Dhaka', value: 'Sundarban Inside Dhaka'},
      {label: 'Sundarban Outside Dhaka', value: 'Sundarban Outside Dhaka'}
    ],
    orderStatusOptions: [
      {label: 'Incomplete', value: 'incomplete'},
      {label: 'Completed', value: 'completed'}
    ]
  },
  getters: {
    orders: state => {
      return state.orders
    },
    fulfillments: state => {
      return state.fulfillmentsOption
    },
    orderStatusOptions: state => {
      return state.orderStatusOptions
    }
  },
  mutations: {
    setOrders: (state, payload) => {
      state.orders = payload
    },
    updateOrder: (state, payload) => {
      for(let item of state.orders) {
        if(item.id === payload.order.id) {
          // update metafields
          item.fulfilled_by = payload.order.fulfilled_by
          item.fulfilled_by_id = payload.order.fulfilled_by_id
          item.order_status = payload.order.order_status
          item.order_status_id = payload.order.order_status_id

          // Update fulfillment info
          item.fulfillment_status = payload.order.fulfillment_status
          item.fulfillment_order_id = payload.order.fulfillment_order_id
          return
        }
      }
    }
  },
  actions: {
    setOrders: ({ commit }, payload) => {
      commit('setOrders', payload)
    },
    updateOrder: ({ commit }, payload) => {
      commit('updateOrder', payload)
    }
  }
})